import React, { Component } from 'react'
import { NavBar, Icon,Carousel,SearchBar,Flex,WhiteSpace } from 'antd-mobile';

export default class AppHome extends Component {
    state = {
        data: ['1', '2','3'],
    }
    componentDidMount() {
      setTimeout(() => {
        this.setState({
          data: ['1', '2','3'],
        });
      }, 100);
    }
    render() {
        return (
            <div>
                <NavBar
                    style={{backgroundColor:'#3fcccb',color:'#000'}}
                    rightContent={[
                        <Icon key="0" type="search" style={{ marginRight: '16px' }} />,
                    ]}
                    >商城</NavBar>
                {/* 搜索框 */}
                <SearchBar 
                    placeholder="输入关键字搜索" 
                    style={{position:"relative",top:"50px",marginTop:"-52px"}}
                />
                {/* 轮播图 */}
                <Carousel
                    autoplay={true} >
                    {this.state.data.map(val => (
                        <img
                            style={{width:'100%',height:'270px'}}
                            key={val}
                            src={require(`../images/shop${val}.jpg`)}
                            alt=""
                            onLoad={() => {
                            window.dispatchEvent(new Event('resize'));
                            this.setState({ imgHeight: 'auto' });
                            }}
                        />
                    ))}
                </Carousel>
                <div style={{margin:'0 auto'}}>
                    <WhiteSpace size="lg" />
                    <Flex style={{margin:'0 15px'}}>
                        <Flex.Item>
                            <img style={{width:'80%',height:'50px'}} src={require(`../images/zhuo.jpg`)} alt=""/>
                            <br/>
                            桌
                        </Flex.Item>
                        <Flex.Item>
                            <img style={{width:'80%',height:'50px'}} src={require(`../images/chuang.jpg`)} alt=""/>
                            <br/>
                            床
                        </Flex.Item>
                        <Flex.Item>
                            <img style={{width:'80%',height:'50px'}} src={require(`../images/yi.jpg`)} alt=""/>
                            <br/>
                            椅
                        </Flex.Item>
                        <Flex.Item>
                            <img style={{width:'80%',height:'50px'}} src={require(`../images/ji.jpg`)} alt=""/>
                            <br/>
                            几
                        </Flex.Item>
                        <Flex.Item>
                            <img style={{width:'80%',height:'50px'}} src={require(`../images/gui.jpg`)} alt=""/>
                            <br/>
                            柜
                        </Flex.Item>
                    </Flex>

                    <Flex style={{margin:'0 15px'}}>
                        <Flex.Item>
                            <img style={{width:'80%',height:'50px'}} src={require(`../images/shujia.jpg`)} alt=""/>
                            <br/>
                            书架
                        </Flex.Item>
                        <Flex.Item>
                            <img style={{width:'80%',height:'50px'}} src={require(`../images/shafa.jpg`)} alt=""/>
                            <br/>
                            沙发
                        </Flex.Item>
                        <Flex.Item>
                            <img style={{width:'80%',height:'50px'}} src={require(`../images/jiaju.jpg`)} alt=""/>
                            <br/>
                            家居
                        </Flex.Item>
                        <Flex.Item>
                            <img style={{width:'80%',height:'50px'}} src={require(`../images/huwai.jpg`)} alt=""/>
                            <br/>
                            户外
                        </Flex.Item>
                        <Flex.Item>
                            <img style={{width:'80%',height:'50px'}} src={require(`../images/quanbu.jpg`)} alt=""/>
                            <br/>
                            全部
                        </Flex.Item>
                    </Flex>  
                </div>
                <div>
                    <WhiteSpace size="lg" />
                    <Flex> 
                        <Flex.Item>
                            <img style={{width:'100%',height:'140px'}} src={require(`../images/cup.jpg`)} alt=""/>
                            <p>Top Art Studio欧式风格精细</p>
                            <p>￥39.95</p>
                        </Flex.Item>
                        <Flex.Item>
                            <img style={{width:'100%',height:'140px'}} src={require(`../images/clock.jpg`)} alt=""/>
                            <p>顺顺工艺欧式风格塑料外框</p>
                            <p>￥39.95</p>
                        </Flex.Item>
                    </Flex>
                </div>
            </div>

        )
    }
}