import React, { Component } from 'react'
import { NavBar, Icon} from 'antd-mobile';

export default class AppMe extends Component {
    render() {
        return (
            <div>
                <NavBar
                    style={{backgroundColor:'#3fcccb',color:'#000'}}
                    rightContent={[
                        <Icon key="0" type="search" style={{ marginRight: '16px' }} />,
                    ]}
                    >我的</NavBar>
            </div>
        )
    }
}
