import React, { Component } from 'react'
import { NavBar, Icon, Tabs,Flex,WhiteSpace} from 'antd-mobile';

// 二级标题
const tabs = [
    { title: '推荐' },
    { title: '冬季'},
    { title: '宜家' },
    { title: '小清新' },
    { title: '小户型' },
    { title: '个性色彩' },
  ];
export default class AppFeel extends Component {
    render() {
        return (
            <div>
                <NavBar
                    style={{backgroundColor:'#3fcccb',color:'#000'}}
                    rightContent={[
                        <Icon key="0" type="search" style={{ marginRight: '16px' }} />,
                    ]}
                    >灵感</NavBar>
                    
                <Tabs tabs={tabs}
                    initialPage={0}
                    onChange={(tab, index) => { console.log('onChange', index, tab); }}
                    onTabClick={(tab, index) => { console.log('onTabClick', index, tab); }}
                    >
                    {/* 推荐页面内容 */}
                    <div>
                        {/* 上下留白 */}
                        <WhiteSpace size="lg" />
                        {/* Flex布局 */}
                        <Flex> 
                            <Flex.Item><img style={{width:'100%',height:'180px'}} src={require(`../images/feel1.jpg`)} alt=""/></Flex.Item>
                            <Flex.Item><img style={{width:'100%',height:'180px'}} src={require(`../images/feel2.jpg`)} alt=""/></Flex.Item>
                        </Flex>
                        <WhiteSpace size="lg" />
                        <Flex> 
                            <Flex.Item><img style={{width:'100%',height:'180px'}} src={require(`../images/feel3.jpg`)} alt=""/></Flex.Item>
                            <Flex.Item><img style={{width:'100%',height:'180px'}} src={require(`../images/feel4.jpg`)} alt=""/></Flex.Item>
                        </Flex>
                        <WhiteSpace size="lg" />
                        <Flex> 
                            <Flex.Item><img style={{width:'100%',height:'180px'}} src={require(`../images/feel1.jpg`)} alt=""/></Flex.Item>
                            <Flex.Item><img style={{width:'100%',height:'180px'}} src={require(`../images/feel2.jpg`)} alt=""/></Flex.Item>
                        </Flex>
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        Content of second tab
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        Content of third tab
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        Content of fourth tab
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        Content of fifth tab
                    </div>
                    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', height: '150px', backgroundColor: '#fff' }}>
                        Content of sixth tab
                    </div>
                    </Tabs>
            </div>
        )
    }
}