import React, { Component } from 'react'
import { NavBar,Icon,Carousel,Flex,WhiteSpace} from 'antd-mobile';
// import { Component } from '@angular/core';
export default class AppHome extends Component {
    state = {
        data: ['1', '2'],
    }
    componentDidMount() {
        // 设置延时切换
        setTimeout(() => {
          this.setState({
            data: ['1','2'],
          });
        }, 50);
    }
    render() {
        return (
            <div>
                <NavBar
                    style={{backgroundColor:'#3fcccb',color:'#000'}}
                    rightContent={[
                        <Icon key="0" type="search" style={{ marginRight: '16px' }} />,
                    ]}
                    >住吧家居</NavBar>

                    {/* 轮播图 */}  
                    <Carousel
                    autoplay={true} >
                    {this.state.data.map(val => (
                        <img
                            style={{width:'100%',height:'270px'}}
                            key={val}
                            src={require(`../images/bed${val}.jpg`)}
                            alt=""
                            onLoad={() => {
                            window.dispatchEvent(new Event('resize'));
                            this.setState({ imgHeight: 'auto' });
                            }}
                        />
                    ))}
                </Carousel>
                <div>
                    {/* 上下留白 */}
                    <WhiteSpace size="lg" />
                    {/* Flex布局 */}
                    <Flex>
                        <Flex.Item><img style={{width:'100%',height:'140px'}} src={require(`../images/home3.jpg`)} alt=""/></Flex.Item>
                        <Flex.Item><img style={{width:'100%',height:'140px'}} src={require(`../images/home4.jpg`)} alt=""/></Flex.Item>
                        <Flex.Item><img style={{width:'100%',height:'140px'}} src={require(`../images/home5.jpg`)} alt=""/></Flex.Item>
                    </Flex>
                </div>
                
                <p>热门推荐</p>
                <img style={{width:'100%',height:'180px'}} alt='' src={require(`../images/home6.jpg`)}></img>
            </div>
        )
    }
}